
.top {
  background-color: #EDEDE6;
  position: fixed;
  width: 100%;
  top: 0;
  height: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.header-left, .header-right {
  display: -ms-flexbox;
  display: flex;
}
.top-logo {
  height: 45px;
  margin: 2px 15px;
}
.top-title {
  font-size: 34px;
  vertical-align: middle;
  margin: auto;
}
.greeting {
  margin: auto;
  font-size: 20px;
}
.top-user-pic {
  border-radius: 50%;
  height: 50px;
  margin: 0 15px;
}
.top-user-pic:hover {
  box-shadow: 0 0 6px black;
  cursor: pointer;
}
.header-list:hover #user-dropdown {
  display: block;
}
#user-dropdown {
  position: absolute;
  background: #faf8fd;
  display: none;
  border-left: 1px solid #ccc;
  border-right: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  overflow: hidden;
  z-index: 10;
  top: 50px;
  font-size: 15px;
  width: 100px;
  color: #4d4e50;
  text-decoration: none;
  margin: auto;
  right: 0;
  cursor: pointer;
  padding: 0;
}
#user-dropdown > li {
  text-align: center;
  padding: 7px;
  border-top: 1px solid #ccc;
  color: #4d4e50;
}
#user-dropdown > li:hover {
  background: #eee;
}



